"use client";

import React, { type JSX } from "react";

interface ButtonProps {
  text: string;
  onClick: () => void;
}

export const Button = ({ text, onClick }: ButtonProps): JSX.Element => {
  return (
    <button
      className="w-full bg-blue-500 px-6 py-3 hover:bg-blue-400 transition-all"
      onClick={onClick}
    >
      {text}
    </button>
  );
};
